import React from "react";
import { Link } from "react-router-dom";

const Navigation = () => (
  <div>
    <div className="top" />
    <div className="navigation-container">
      <img
        className="logo"
        src="https://www.shareicon.net/data/256x256/2016/01/07/699720_computer_512x512.png"
        alt="logo"
      />
      <h2 className="navigation--heading">James Au's playground</h2>
      <div className="icon">
        <Link to="/" className="fa fa-home" aria-hidden="true">
          <span className="icon__text">Home</span>
        </Link>
        <Link to="/blog" className="fa fa-file-text" aria-hidden="true">
          <span className="icon__text">Blog</span>
        </Link>
        <Link to="/about" className="fa fa-info-circle" aria-hidden="true">
          <span className="icon__text">About</span>
        </Link>
        <Link to="/social-media" className="fa fa-users" aria-hidden="true">
          <span className="icon__text">Social Media</span>
        </Link>
      </div>
    </div>
  </div>
);

export default Navigation;



